import OnekitWX_BackgroundFetch from "./BackgroundFetch"
export default class OnekitWX_Storage extends OnekitWX_BackgroundFetch{
  // INFO: 
  static setStorageSync(key,value) {
    try {
      localStorage.setItem(key, value);
    } catch (e) {}
  }

  // INFO: 
  static setStorage(object) {
    var key = object.key;
    var data = object.data;
    var success = object.success;
    var fail = object.fail;
    var complete = object.complete;

    var res = {};
    try {
      localStorage.setItem(key, data);
      if (localStorage.getItem(key) === data) {
        res.errMsg = 'setStorage:ok';
        if (success) {
          success(res);
        }
      }
    } catch (error) {
      res.errMsg = error.message;
      if (fail) {
        fail(res);
      }
    }
    if (complete) {
      complete(res);
    }
  }
  // INFO: 
  // INFO: try catchtry catch, https://developers.weixin.qq.com/miniprogram/dev/api/wx.getStorageSync.html
  static getStorageSync(key) {
    return localStorage.getItem(key);
  }

  // INFO: 
  static getStorage(object) {
    var key = object.key;
    var success = object.success;
    var fail = object.fail;
    var complete = object.complete;

    var res = {};
    try {
      var value = localStorage.getItem(key);
      if (value) {
        res.errMsg = 'getStorage:ok';
        res.data = value;
        if (success) {
          success(res);
        }
      } else {
        res.errMsg = 'getStorage:fail data not found';
        if (fail) {
          fail(res);
        }
      }
    } catch (error) {
      res.errMsg = error.message;
      if (fail) {
        fail(res);
      }
    }
    if (complete) {
      complete(res);
    }
  }

  // INFO: 
  // INFO: try catchtry catch, https://developers.weixin.qq.com/miniprogram/dev/api/wx.removeStorageSync.html
  static removeStorageSync(key) {
    localStorage.removeItem(key);
  }

  // INFO: 
  static removeStorage(object) {
    var key = object.key;
    var success = object.success;
    var fail = object.fail;
    var complete = object.complete;

    var res = {};
    try {
      localStorage.removeItem(key);
      res.errMsg = 'removeStorage:ok';
      if (success) {
        success(res);
      }
    } catch (error) {
      res.errMsg = error.message;
      if (fail) {
        fail(res);
      }
    }
    if (complete) {
      complete(res);
    }
  }

  // INFO: 
  // INFO: try catchtry catch, https://developers.weixin.qq.com/miniprogram/dev/api/wx.clearStorageSync.html
  static clearStorageSync() {
    localStorage.clear();
  }

  // INFO: 
  static clearStorage(object) {
    if (!object) {
      localStorage.clear();
    } else {
      let success = object.success || '';
      let fail = object.fail || '';
      let complete = object.complete || '';

      var res = {};
      try {
        localStorage.clear();
        res.errMsg = 'clearStorage:ok';
        if (success) {
          success(res);
        }
      } catch (error) {
        res = { errMsg: error.message };
        if (fail) {
          fail(res);
        }
      }
      if (complete) {
        complete(res);
      }
    }
  }

  static getStorageInfo(object) {
    let success = object.success;
    let fail = object.fail;
    let complete = object.complete;
    var result;
    var keysArray = new Array();
    for (var i = 0; i < localStorage.length; i++) {
      //
      var getKey = localStorage.key(i);
      keysArray.push(getKey);
    }
    var sizeStore = 0;
    if (localStorage) {
      //
      for (let item in localStorage) {
        if (localStorage.hasOwnProperty(item)) {
          sizeStore += localStorage.getItem(item).length;
        }
      }
    }
    try {
      result = {
        keys: keysArray,
        // HACK: 
        currentSize: Math.ceil((sizeStore / 1024).toFixed(2)),
        // TODO:  key  1MB 10MB
        limitSize: ''
      };
      if (success) {
        success(result);
      }
      if (complete) {
        complete(result);
      }
    } catch (e) {
      result = { errMsg: e.message };
      if (fail) {
        fail(result);
      }
      if (complete) {
        complete(result);
      }
    }
  }

  static getStorageInfoSync() {
    var result;
    try {
      var keysArray = new Array();
      for (var i = 0; i < localStorage.length; i++) {
        //
        var getKey = localStorage.key(i);
        keysArray.push(getKey);
      }
      var sizeStore = 0;
      if (localStorage) {
        //
        for (let item in localStorage) {
          if (localStorage.hasOwnProperty(item)) {
            sizeStore += localStorage.getItem(item).length;
          }
        }
      }
      result = {
        keys: keysArray,
        // HACK: 
        currentSize: Math.ceil((sizeStore / 1024).toFixed(2)), // kb
        // TODO:  key  1MB 10MB
        limitSize: ''
      };
      return result;
    } catch (e) {
      throw new Error(e.message);
    }
  }
}
