import OnekitWX_Monitor from "./Monitor"
export default class OnekitWX_Payment extends OnekitWX_Monitor{
  static requestPayment(object) {
    // 
    let timestamp = object.timestamp; //  1970  1  1  00:00:00 
    let nonceStr = object.nonceStr; // 32
    let package_s = object.package; //  prepay_id prepay_id=*** package js package_s 
    let signType = object.signType; // 
    let paySign = object.paySign; // 
    let success = object.success || '';
    let fail = object.fail || '';
    let complete = object.complete || '';
    wx.chooseWXPay({
      // JS-SDK
      timestamp: timestamp, // jssdktimestamptimeStampS
      nonceStr: nonceStr, //  32 
      package: package_s, // prepay_idprepay_id=\*\*\*
      signType: signType, // 'SHA1''MD5'
      paySign: paySign, // 
      success: success(res),
      fail: fail(res),
      complete: complete(res)
    });
  }

}
