import OnekitWX_Log from "./Log"
export default class OnekitWX_System extends OnekitWX_Log{
  static getSystemInfo(object) {
    let success = object.success;
    let fail = object.fail;
    let complete = object.complete;
    /////////////////////////////
    var result;
    try {
      var device_type = navigator.userAgent; //userAgent
      var md = new MobileDetect(device_type); //mobile-detect
      var os = md.os(); //
      var model = '';
      var system = '';
      var platform = '';
      if (os === 'iOS') {
        //ios
        system = 'iOS ' + md.version('iPhone');
        model = md.mobile();
        platform = 'ios';
      } else if (os === 'AndroidOS') {
        //Android
        system = 'Android ' + md.version('Android');
        model = md.mobile();
        platform = 'android';
      }
      result = {
        errMsg: 'getSystemInfo:ok',
        brand: 'Unknown', // 
        model: model, // 
        pixelRatio: window.devicePixelRatio, // 
        screenWidth: window.screen.width, // 
        screenHeight: window.screen.height, // 
        windowWidth: window.innerWidth, // 
        windowHeight: window.innerHeight, // 
        statusBarHeight: 'Unknown', // 
        language: window.navigator.language, // 
        version: 'Unknown', // 
        system: system, // 
        platform: platform, // 
        fontSizeSetting: 'Unknown', // --- px
        SDKVersion: 'Unknown', // 
        benchmarkLevel: 'Unknown' // (Android) -2  0-1>=1  (50)
      };
      if(success){success(result);}
      if(complete){complete(result);}
    } catch (e) {
      result = { errMsg: e.message };
      if(fail){fail(result);}if(complete){complete(result);}
    }
  }

  static getSystemInfoSync() {
    var result;
    try {
      var device_type = navigator.userAgent; //userAgent
      var md = new MobileDetect(device_type); //mobile-detect
      var os = md.os(); //
      var model = '';
      var system = '';
      var platform = '';
      if (os == 'iOS') {
        //ios
        system = 'iOS ' + md.version('iPhone');
        model = md.mobile();
        platform = 'ios';
      } else if (os == 'AndroidOS') {
        //Android
        system = 'Android ' + md.version('Android');
        model = md.mobile();
        platform = 'android';
      }
      result = {
        errMsg: 'getSystemInfoSync:ok',
        brand: 'Unknown', // 
        model: model, // 
        pixelRatio: window.devicePixelRatio, // 
        screenWidth: window.screen.width, // 
        screenHeight: window.screen.height, // 
        windowWidth: window.innerWidth, // 
        windowHeight: window.innerHeight, // 
        statusBarHeight: 'Unknown', // 
        language: window.navigator.language, // 
        version: 'Unknown', // 
        system: system, // 
        platform: platform, // 
        fontSizeSetting: 'Unknown', // --- px
        SDKVersion: 'Unknown', // 
        benchmarkLevel: 'Unknown' // (Android) -2  0-1>=1  (50)
      };
      return result;
    } catch (e) {
      return (result = { errMsg: 'getSystemInfoSync:fail' });
    }
  }
}
