import OnekitWX_MediaContainer from "./MediaContainer"
export default class OnekitWX_Record extends OnekitWX_MediaContainer{
  static startRecord(object) {
    let success = object.success;
    let fail = object.success;
    let complete = object.success;
    //////////////////////////////
    try {
      wx.startRecord();
    } catch (error) {
      result.errMsg = error.message;
      console.log(result);
    }
  }

  static stopRecord(object) {
    let success = object.success;
    let fail = object.success;
    let complete = object.success;
    //////////////////////////////
    let result = {};
    let localId;
    wx.stopRecord({
      success: function(res) {
        if (success) {
          result.errMsg = 'startRecord:ok';
          result.tempFilePath = res.localId; //  tempFilePath JS-SDK localId  localId  tempFilePath tempFilePath 
          success(result);
        }
      },
      fail: function(result) {
        fail(result);
      },
      complete: function(result) {
        if (complete) {
          complete(result);
        }
      }
    });
  }
}
